import "./App.scss";
import { useLS } from "./Hooks/useLS";
import { useState, useEffect } from "react";
import { Form } from "./Components/Forms/Form";
import { ProductList } from "./Components/Products/ProductList";
import { Modal } from "./Components/Modal/Modal";
import { ImagesList } from "./views/PexelsImages/ImagesListClass";
import { SearchForm } from "./views/PexelsImages/SearchForm";
function App() {
  const [counter, setCounter] = useState(0);
  const [isOpen, setIsOpen] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [allProducts, setAllProducts] = useState("products", []);
  const [searchValue, setSearchValue] = useState("");
  const [perPage, setPerPage] = useState(5);

  const addNewProduct = (obj) => setAllProducts((zuzuzu) => [...zuzuzu, obj]);
  const deleteProduct = (id) =>
    setAllProducts((zuzuzu) => zuzuzu.filter((prod) => prod.id !== id));

  const toggleModal = () => {
    setShowModal(!showModal);
  };

  const getSearchValues = (searchValue, perPage) => setSearchValue(searchValue);
  setPerPage(perPage);
  return (
    <div className="App">
      <SearchForm getSearchValues={getSearchValues} />
      <ImagesList searchValue={searchValue} perPage={perPage} />

      {showModal && (
        <Modal toggleModal={toggleModal}>
          <Form addNewProduct={addNewProduct} />
        </Modal>
      )}
      <h1>FE-35 Product</h1>
      <button type="button" onClick={toggleModal}>
        Add product
      </button>
      <ProductList products={allProducts} onDeleteProduct={deleteProduct} />
    </div>
  );
}
export default App;
